<template>
  <div class="lang_transfer_box" id="lang_transfer_box">
    <div class="eng" :style="global.lang==='en'?'color: #251714;':''" @click="change_lang('en')">Eng</div>
    <div class="separator"></div>
    <div class="tc" :style="global.lang==='tc'?'color: #251714;':''" @click="change_lang('tc')">繁</div>
    <div class="separator"></div>
    <div class="cn" :style="global.lang==='cn'?'color: #251714;':''" @click="change_lang('cn')">简</div>
  </div>
</template>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

div{
  font-family: HelveticaNeueLTProLtEx;
}

div.lang_transfer_box {
  position: fixed;
  z-index: 99;
  top: 50rem;
  right: 150rem;
  font-size: 20rem;
  line-height: 1;
  background: transparent;
  transform: translate(50%, 0);
  display: flex;
  flex-direction: row;
}

div.separator{
    width: 1rem;
    height: 20rem;
    background: #251714;
    opacity: 70%;
}

div.eng, div.tc, div.cn{
    color:#898989;
    cursor: pointer;
    padding-left: 5rem;
    padding-right: 5rem;
    padding-bottom: 1rem;
}

div.eng:hover, div.tc:hover, div.cn:hover {
    color: #efcd24;
}

</style>


<script>
import global from "../../pages/global";
import anime from "../../../modules/anime";

let vm = {
  name: "LangTransfer",
  data() {
    return {
      global:global,
    };
  },
  beforeMount() {},
  mounted() {
    window.onresize = () => {
      return (() => {
        this.refresh_root_size();
      })();
    };
  },
  destroyed() {},
  methods: {
    refresh_root_size() {
      let static_piexl = 960;
      let width =
        document.documentElement.clientWidth || document.body.clientWidth;
      if (width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize =
        document.documentElement.clientWidth / 1920 + "px";
      if (is_show_menu) {
        this.show_menu();
        this.change_menu_clock_circle();
      }
    },
    change_lang(lang){
        global.lang = lang
        global.change_lang()
    }
  },
};

export default vm;
</script>



